import $ from 'jquery';

require('./vendor/jquery.gallery.js');
require('jquery-columnizer');


$(document).ready(function () {

    $('.dg-container').gallery();
    $('.columnize-js').columnize({
        columns: 2
    });


    $(".nav a").click(function () {
        var elementClick = $(this).attr("href");
        var destination = $(elementClick).offset().top - 100;
        $('html,body').animate( { scrollTop: destination }, 1100 );
        return false;
    });

    $(document).scroll(function () {
        var s_top = $("body").scrollTop();
        var yes = $("#section-form").offset().top;
        if(s_top > yes){
            console.log("Yes");
            $('.section-form__form').animate({width: "1030px"}, 1000);
            $('.section-form .center').animate({opacity: "1"}, 2000);
            $('.section-form__form').animate({opacity: "1"}, 2000);

        }
    });
});




